/*
  This file is purely for generating the CK editor stylesheet, which may contain typography and various other things to be added in the CK iframe
*/
@tailwind base;
@tailwind components;
@tailwind utilities;
/* Heading 1 */
h1 {
  @apply font-bold;
  @apply text-24;
  @apply leading-115;
  @apply tracking-03;
  @apply mt-16;
  @apply mb-24; }

@screen md {
  h1 {
    @apply text-32; } }

@screen lg {
  h1 {
    @apply text-44;
    @apply mb-32; } }

/* Heading 2 */
h2 {
  @apply mt-24;
  @apply mb-8;
  @apply tracking-03;
  @apply font-medium;
  @apply leading-145;
  @apply text-19; }

@screen md {
  h2 {
    @apply text-22; } }

@screen lg {
  h2 {
    @apply text-24;
    @apply tracking-03; } }

/* Heading 3 */
h3 {
  @apply mt-24;
  @apply mb-8;
  @apply tracking-03;
  @apply font-medium;
  @apply leading-145;
  @apply text-18; }

@screen md {
  h3 {
    @apply text-20; } }

@screen lg {
  h3 {
    @apply text-22; } }

/*
// Combinations
*/
h2 + h3 {
  @apply mt-16; }

@screen md {
  h2 + h3 {
    @apply mt-24; } }

p + h2 {
  @apply mt-24; }

@screen md {
  p + h2 {
    @apply mt-32; } }

body {
  @apply font-body;
  @apply p-16; }

